let app = {};
app.login = function(loginInfo, callback) {
    callback = callback || $.noop;
    loginInfo = loginInfo || {};
    loginInfo.mobile = loginInfo.mobile || '';
    loginInfo.password = loginInfo.password || '';
    /************************************************************************/
    /*                                                           */
    /************************************************************************/
    loginInfo.device = plus.device.vendor || ''; //
    loginInfo.model = plus.device.model || ''; //
    loginInfo.screen = plus.screen.resolutionWidth + ',' + plus.screen.resolutionHeight || ''; //
    loginInfo.sys = plus.os.name || ''; //
    loginInfo.version = plus.os.version || ''; //
    loginInfo.no = plus.device.uuid || ''; //id
    /************************************************************************/
    /*                             */
    /************************************************************************/
    var phonereg = /^$/;
    if (phonereg.test(loginInfo.mobile)) {
        return callback('');
    }
    /************************************************************************/
    /*                            */
    /************************************************************************/
    var passwordreg = /^$/;
    if (passwordreg.test(loginInfo.password)) {
        return callback('');
    }
    /************************************************************************/
    /*ajax                              */
    /************************************************************************/
    mui.ajax('https://api.gaoqi.cespc.com:9378/user/login', {
        data: loginInfo,
        dataType: 'json', //json
        type: 'post', //HTTP
        timeout: 10000, //10
        success: function(data) {
            if (data.ret == 1) {
                var obj = {};
                obj.ticket = data.ticket;
                obj.expire_timestamp = data.expire_timestamp;
                obj.user = data.user;
                console.log(JSON.stringify(obj));
                app.setState(obj); //
                app.setAllReginfo(obj); //  
                // app.setReginfo('ticket', obj.ticket); 
                // app.setReginfo('expire_timestamp', obj.expire_timestamp);
                // app.setReginfo('user', obj.user);
                console.log('');
                //
                return callback();

            } else {
                callback(data.err);
            }
        },
        error: function(xhr, type, errorThrown) {
            //
            if (type == 'timeout') {
                plus.nativeUI.toast('');
            } else if (type == 'abort') {
                plus.nativeUI.toast('');
            } else if (type == 'timeout') {
                plus.nativeUI.toast('');
            }
            console.log(type);
        }
    });

};




